.logo {
    width: 5%;
    margin: auto;
  }
  
  .logoContainer {
    animation: fadeIn 5s;
    -webkit-animation: fadeIn 5s;
    -moz-animation: fadeIn 5s;
    -o-animation: fadeIn 5s;
    -ms-animation: fadeIn 5s;
  }
  
  .logoContainer {
    animation-name: example;
    animation-duration: 3s;
    animation-delay: 2s;
    animation-fill-mode: both;
  }
  
  @keyframes example {
    0% {
      background-color: #555555;
      top: 0px;
    }
    50% {
      background-color: #2a7e5f;
      top: 0px;
    }
    100% {
      background-color: #033333;
      top: 0px;
    }
  }
  
  .chatBotHeader {
    color: white;
    height: 5.5rem;
    background-color: black;
    display: grid;
    padding: 0.8rem;
    grid-template-columns: 40% 60%;
    text-align: center;
  }
  
  .chatBotHeader input {
    width: 50%;
    margin: auto;
    height: 50px;
    text-align: center;
    border-radius: 15px;
  }
  
  .chatbotBody {
    height: 82.1vh !important;
    overflow: auto !important;
  }
  
  .chatbotFooter {
    display:flex;
    /* position: absolute;
    bottom: 0; */
    width: 100%;
    height: 3.5rem;
    background-color: black;
  }
  
  .chatbotFooter p {
    margin:auto
  }
  
  .introMessage {
    width: 100%;
    border-radius: 35px;
    padding: 1rem;
    text-align: center;
    color: white;
  }
  
  .introMessage p {
    border: 1px solid grey;
    border-radius: 10px;
    padding: 1rem;
    font-size: 1.3rem;
    font-weight: bold;
    color: black;
  }
  
  .treeOptions div {
    padding: 1rem !important;
  }
  
  .treeOptions div button {
    text-align: left;
    padding: 1rem !important;
    border: 2px solid rgb(29, 198, 186);
    border-radius: 1rem;
    width: 40%;
    color: black;
    /* border: 2px solid rgb(173, 168, 168); */
  }
  
  .treeOptions div button:hover {
    /* color: white; */
    text-align: left;
    padding: 1rem !important;
    border: 2px solid rgb(29, 198, 186);
    border-radius: 1rem;
    width: 40%;
    color: black;
    box-shadow: 0 0 20px #3da35d;
    border-radius: 15px;
    padding: 0.5rem;
    /* border: 2px solid rgb(173, 168, 168); */
  }
  
  .p-tag-text{
    padding: .5rem;
    width: 40%;
    border: 1px solid grey;
    border-radius: 10px;
    width:100%;
    text-align: center;
    margin-top:3rem;
  }
  
  
  .p-tag-text1{
    float: right;
    color:white;
    background-image: linear-gradient(to right, purple , #6495ED);
    /* text-align: center !important; */
    width: 40%;
    padding: .5rem;
    border-radius: 10px;
  
  }
  
  .optionButtonsContainer{
  /* padding-bottom:2rem; */
  }
  
  .optionButtons {
    text-align: left;
    padding: 1rem !important;
    border: 2px solid rgb(29, 198, 186);
    border-radius: 1rem;
    width: 40%;
    color: black;
    
  }
  
  .optionButtons:hover {
    text-align: left;
    padding: 1rem !important;
    border: 2px solid rgb(29, 198, 186);
    border-radius: 1rem;
    width: 40%;
    color: black;
    box-shadow: 0 0 20px #3da35d;
    border-radius: 15px;
    padding: 0.5rem;
  }
  
  .form-control {
    width: 100% !important;
  }
  
  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .form {
    width: 350px;
    padding: 2rem 2rem;
    border-radius: 0.5rem;
    /* border: 1px solid black; */
    box-shadow: 0 10px 25px Rgba(92, 99, 105, 0.2);
    margin-bottom: 2rem;
  }
  
  .form-label {
    display: flex;
    margin-left: 0rem;
    margin-top: 0.8rem;
  }
  
  :root {
    --first-color: hsl(224, 82%, 51%);
    --input-color: #80868b;
    --border-color: #dadce0;
    --normal-font-size: 1rem;
    --samll-font-size: 0.75rem;
  }
  
  .form-button {
    display: block;
    margin-top: 1.5rem;
    margin-left: 5%;
    padding: 0.5rem 1rem;
    outline: none;
    border: none;
    background-color: var(--first-color);
    color: #fff;
    font-size: var(--normal-font-size);
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.3s;
    width: 60%;
  }
  
  .form-button:hover {
    box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
  }
  
  .form-textarea {
    width: 90%;
    height: 100%;
    border-radius: 0.5rem;
    padding: 1rem;
  }
  
  .addAstericks:after {
    content: " *";
    color: red;
    width: 5%;
  }
  
  .noneAstericks {
    width: 90%;
    margin-top: 0.8rem !important;
    padding-bottom: 1rem;
  }
  
  input[type="date"] {
    width: 90%;
    padding: 0.2rem;
  }
  
  .applicant-info {
    width: 350px;
    padding: 2rem 2rem;
    border-radius: 0.5rem;
    /* border: 1px solid black; */
    box-shadow: 0 10px 25px Rgba(92, 99, 105, 0.2);
  }
  
  .info li {
    display: grid;
  }
  
  .scrollbar-hidden {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  
  ::-webkit-scrollbar {
    display: none;
  }
  
  .selectInput {
    margin: auto !important;
    display: flex;
    width: 25% !important;
  }
  
  .applicantInfomationContainer {
    border: 5px solid #033333;
    width: 80%;
    border-radius: 10px;
    margin: auto;
    padding: 1rem;
    background-color: #033333;
  }
  
  
  
  .applicantInfomation {
    background-color: white;
    border: 5px solid #033333;
    border-radius: 10px;
    margin: auto;
    padding: 2rem;
  }
  
  .applicantInfomation ul {
    display: grid;
    flex-direction: column;
    text-align: left !important;
    margin: 0 !important;
    padding-left: 0rem;
  }
  .applicantInfomation p {
    color: #033333 !important;
    text-align: left !important;
  }
  
  .applicantInfomationButtons {
    display: grid;
    grid-template-columns: auto auto;
    width: 100%;
  }
  
  .applicantInfomationButtons input {
    color: #033333;
    margin: auto;
    width: 80%;
  }
  
  .applicantInfomationButtons input:hover{
    color: #033333;
    margin: auto;
    width: 80%;
  }
  
  .headerButtons {
    cursor: pointer;
  }
  
  .headerButtons:hover {
    box-shadow: 0px 0px 30px;
    border: 2px solid rgb(29, 198, 186);
  }
  